import { useEffect, useState } from "react";

const useKeyValue = ({ spreadsheetId, sheetName }) => {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [data, setData] = useState(null);

    const parseCSVKeyValue = (csv) => {
        const rows = csv.trim().split("\n");

        return rows.reduce((acc, row) => {
            const columns = row.match(/(".*?"|[^",\s]+)(?=\s*,|\s*$)/g) // Regex to split by commas, ignoring commas inside quotes
            ?.map(col => col.replace(/(^"|"$)/g, "").trim());

            if (!columns || columns.length === 0) return acc;

            const key = columns[0]; // First column as key
            if (!key) return acc; // Skip rows with no key

            const values = columns.slice(1).filter(value => value !== ""); // Remove empty strings

            acc[key] = values.length === 1 ? values[0] : values; // Single value or array
            return acc;
        }, {});
    };

    useEffect(() => {
        const fetchSpreadsheetData = async () => {
            const url = `https://docs.google.com/spreadsheets/d/${spreadsheetId}/gviz/tq?tqx=out:csv&sheet=${sheetName}`;

            try {
                const response = await fetch(url);
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                const csvText = await response.text();
                const jsonData = parseCSVKeyValue(csvText);

                setData(jsonData);
                setLoading(false);
            } catch (err) {
                console.error("Error fetching spreadsheet data:", err);
                setError(err.message);
                setLoading(false);
            }
        };

        fetchSpreadsheetData();
    }, [spreadsheetId, sheetName]);

    return { loading, error, data };
};

const useKeyValueArray = ({ spreadsheetId, sheetName }) => {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [data, setData] = useState([]);

    const parseCSVKeyValueArray = (csv) => {
        const rows = csv.trim().split("\n");

        return rows.map(row => {
            const columns = row
                .match(/(".*?"|[^",\s]+)(?=\s*,|\s*$)/g) // Regex to split by commas, ignoring commas inside quotes
                ?.map(col => col.replace(/(^"|"$)/g, "").trim());
            if (!columns || columns.length < 2) return null; // Ignore rows without at least two columns
            return [columns[0], columns[1]]; // Return as [columnA, columnB]
        }).filter(Boolean); // Filter out null rows
    };

    useEffect(() => {
        const fetchSpreadsheetData = async () => {
            const url = `https://docs.google.com/spreadsheets/d/${spreadsheetId}/gviz/tq?tqx=out:csv&sheet=${sheetName}`;

            try {
                const response = await fetch(url);
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                const csvText = await response.text();
                const jsonData = parseCSVKeyValueArray(csvText);

                setData(jsonData);
                setLoading(false);
            } catch (err) {
                console.error("Error fetching spreadsheet data:", err);
                setError(err.message);
                setLoading(false);
            }
        };

        fetchSpreadsheetData();
    }, [spreadsheetId, sheetName]);

    return { loading, error, data };
};

const useMenu = ({ spreadsheetId, sheetName }) => {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [data, setData] = useState([]);

    const parseCSVMenu = (csv) => {
        const rows = csv.trim().split("\n");

        const headers = rows
        .shift()
        .match(/(".*?"|[^",\s]+)(?=\s*,|\s*$)/g)
        ?.map(h => h.replace(/(^"|"$)/g, ""));

        if (!headers) return [];

        return rows.map(row => {
            const values = row
            .match(/(".*?"|[^",\s]+)(?=\s*,|\s*$)/g)
            ?.map(value => value.replace(/(^"|"$)/g, ""));

            const obj = {};
            headers.forEach((header, index) => {
                if (header) {
                    obj[header] = values?.[index] || null;
                }
            });
            return obj;
        });
    };

    useEffect(() => {
        const fetchSpreadsheetData = async () => {
            const url = `https://docs.google.com/spreadsheets/d/${spreadsheetId}/gviz/tq?tqx=out:csv&sheet=${sheetName}`;

            try {
                const response = await fetch(url);
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                const csvText = await response.text();
                const jsonData = parseCSVMenu(csvText);

                setData(jsonData);
                setLoading(false);
            } catch (err) {
                console.error("Error fetching spreadsheet data:", err);
                setError(err.message);
                setLoading(false);
            }
        };

        fetchSpreadsheetData();
    }, [spreadsheetId, sheetName]);

    return { loading, error, data };
};

export {
    useKeyValue,
    useKeyValueArray,
    useMenu,
};
